import React, { Fragment } from 'react';
import { cta } from '../data/cta';

import { Link } from 'react-router-dom';

const CTA = () => {
  return (
    <Fragment>
      {cta.map((item, index) => {
        return (
          <div key={index} className='my-5'>
            <div className='row featurette text-center'>
              <div className={`col-md-7  ${item.order_1}`}>
                <h2 className='featurette-heading'>
                  {item.title} <br />
                  <Link to={item.byline_link} className='cta-links'>
                    <span>{item.byLine}</span>
                  </Link>
                </h2>
                <p className='lead'>{item.message}</p>
              </div>
              <div className={`col-md-5 ${item.order_2}`}>
                <img
                  src={`${process.env.PUBLIC_URL}/assets/${item.image}`}
                  width='375'
                  alt={item.alt}
                />
              </div>
            </div>
          </div>
        );
      })}
    </Fragment>
  );
};

export default CTA;
